.comments {
   order: 4;
}
.comments .comments-content-container.show {
   overflow: initial;
}
.comments .comments-content-container {
   height: 0;
   transition: height 0.1s linear;
   overflow: hidden;
}

.view-more-comments {
   cursor: pointer;
}
.comments form {
   --height: 25px;
   min-height: var(--height);
   margin: 15px 0 0px 0;
   display: flex;
   align-items: center;
   gap: 10px;
   padding: 10px;
   border-radius: 20px;
}

.comments form .user {
   align-self: flex-start;
}

.comments .user .image-container {
   width: 20px;
   height: 20px;
}

.comments form textarea {
   height: var(--height);
   /* padding-top: calc(var(--height) / 2 - 8px); */
   flex: 1;
   background: transparent;
   resize: vertical;
   resize: none;
   font-family: system-ui;
   font-size: 16px;
}

.comments .icons {
   height: 100%;
   display: flex;
   gap: 10px;
   align-self: flex-end;
   flex-direction: row;
   align-content: center;
   justify-content: center;
   align-items: center;
}

.comments .choose-emojie {
   position: relative;
}

.comments button {
   border-radius: 100%;
   background-color: transparent;
   cursor: pointer;
}

.comments div svg {
   display: grid;
   place-items: center;
   width: 22px;
   cursor: pointer;
}
.comments button svg {
   display: grid;
   place-items: center;
   width: 22px;
   cursor: pointer;
}
.comments ul {
   margin: 10px 0;
   display: flex;
   flex-direction: column-reverse;
   gap: 5px;
}

.comments ul .single-comment {
   display: flex;
   gap: 5px;
}

.comments .single-comment .user {
}

.comments .single-comment .comment-content {
   padding: 3px;
   width: 100%;
}

.single-comment .comment-content a:hover {
   text-decoration: underline;
}

.comments .single-comment .username {
   text-transform: capitalize;
   display: flex;
   flex-direction: row;
   flex-wrap: nowrap;
   align-content: center;
   justify-content: flex-start;
   align-items: center;
   width: 100%;
}

.comments .single-comment .created-at {
   color: gray;
   font-size: 12px;
   font-weight: 400;
   margin: 0 10px;
}

.comments .single-comment .settings {
   margin-left: auto;
}

.comments .single-comment .action-buttons {
   display: none;
}
