.more-modal.active {
   display: block;
}
.more-modal {
}

.more-modal-container {
   z-index: 5;
   position: absolute;
   right: 10px;
   top: 30px;
   max-height: 400px;
   width: 250px;
   background: white;
   border-radius: 20px;
   box-shadow: rgb(101 119 134 / 20%) 0px 0px 15px,
      rgb(101 119 134 / 15%) 0px 0px 3px 1px;
}

.more-modal-container ul {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: flex-start;
}

.more-modal-container li {
   width: 100%;
   display: inherit;
   justify-content: flex-start;
   align-items: center;
   gap: 13px;
   color: #535353;

   padding: 15px 13px;
   cursor: pointer;
   transition: background-color 0.1s linear;
}

.more-modal-container li:hover {
   background-color: rgb(243, 243, 243);
   color: #282828;
}

.more-modal-container span {
}
.more-modal-container svg {
   color: rgb(83, 100, 113);
   width: 20px;
   height: 20px;
}

.more-modal-container li.delete {
   color: red;
}

.more-modal-container li.delete svg {
   color: inherit;
}
