.conversation-box-popup {
  width: 320px;
  bottom: 0;
  right: 100px;
  border: 1px solid rgba(128, 128, 128, 0.075);
  position: fixed;
  z-index: 10;
  border-radius: 20px 20px 0 0;
  transition: right 0.1s;
}

.conversation-box-popup > * {
  padding: 10px 15px;
}

/* ---------------conversation HEADER --------------- */
.conversation-box-popup .header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  text-transform: capitalize;
}
.conversation-box-popup .header .room-data.active {
  opacity: 1;
}
.conversation-box-popup .header .room-data {
  position: absolute;
  top: -21%;
  left: -32px;
  max-height: 270px;
  min-width: 160px;
  border-radius: 15px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
  color: var(--color-light);
  background: var(--color-dark);
  font-size: 12px;
  text-transform: capitalize;
  opacity: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  transition: opacity 0.2s;
}

.conversation-box-popup .header .room-title {
  position: relative;
  margin-bottom: 5px;
}

.conversation-box-popup .header .room-title:after {
  content: '';
  width: 8px;
  height: 8px;
  background: #57df57a6;
  top: 35%;
  right: 0;
  position: absolute;
  border-radius: 100%;
}

.conversation-box-popup .header .user {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2px;
}

.conversation-box-popup .header .image-container {
  width: 40px;
  height: 40px;
}
.conversation-box-popup .header .user-info {
  flex-grow: 1;
  padding: 5px;
}

.conversation-box-popup .user-info .username {
  font-size: 14px;
}
.conversation-box-popup .user-info .status {
  font-size: 11px;
  color: rgb(0, 141, 82);
}

.conversation-box-popup .header .right-section .icons {
  justify-content: center;
  align-items: center;
  display: flex;
}

.conversation-box-popup .header .right-section .icons > div {
  display: flex;
}

.conversation-box-popup svg:not(.user svg) {
  padding: 5px;
  height: 28px;
  width: 28px;
  font-size: 20px !important;
  border: 1px solid rgb(202, 202, 202);
  border-radius: 10px;
  margin: 2px;
  cursor: pointer;
  transition: background 0.1s ease-in-out;
}
.conversation-box-popup svg:active:not(.user svg) {
  background-color: rgba(70, 87, 180, 0.603);
}
.conversation-box-popup svg:hover:not(.user svg) {
  background-color: steelblue;
  color: white;
  border: 1px solid transparent;
}

/* ---------------conversation HEADER --------------- */
.conversation-content {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-content: flex-start;
  align-items: baseline;
  /* gap: 20px; */
  gap: 30px;
  padding: 10px auto;
  height: 50vh;
  max-height: 400px;
  overflow-y: auto;
}

.conversation-info {
  text-align: center;
  padding: 5px 10px;
  color: rgb(182, 182, 182);
  font-size: 13px;
  border-radius: 20px;
  align-self: center;
  font-size: 10px;
  text-transform: capitalize;
  background-color: var(--color-light);
}
ul.conversation-content .message {
  max-width: 100%;
  display: flex;
  gap: 5px;
  flex-direction: row;
  align-items: center;
  width: 100%;
}

ul.conversation-content .message .message-content {
  max-width: 65%;
  align-items: center;
  position: relative;
}

.conversation-content .message .message-caption {
  text-align: left;
  padding: 15px;
  /* background-color: rgb(241, 241, 241); */
  /* color: #222222; */
  border-radius: 25px 25px 25px 1px;
  font-weight: 400;
  font-size: 12px;
  word-wrap: break-word;
  display: flex;
  align-items: center;
}

.conversation-content .message-caption::after,
.conversation-content .message-caption::before {
  position: absolute;
  color: rgb(185, 185, 185);
  text-transform: capitalize;
  word-wrap: normal;
  white-space: nowrap;
}
.conversation-content .message-caption::after {
  content: attr(data-timestamp);
  font-size: 9px;
  bottom: -17px;
}
.conversation-content .message-caption::before {
  content: attr(data-username);
  font-size: 10px;
  left: 110%;
  top: 50%;
  transform: translateY(-50%);
}

.conversation-content .message.own {
  align-self: self-end;
  flex-direction: row-reverse;
}
.conversation-content .message.own .message-caption {
  background-color: #2176ff;
  color: whitesmoke;
  border-radius: 25px 25px 1px 25px;
}

.conversation-content .message img {
  width: 15px;
}

.conversation-content .message .mesage-timestamp {
  position: absolute;
  bottom: -20px;
  font-size: 10px;
  color: rgb(185, 185, 185);
  text-transform: uppercase;
}
/* more btns */
.conversation-content .message .more.show {
  visibility: visible;
}
.conversation-content .message .more {
  visibility: hidden;
  display: inherit;
  align-items: inherit;
}
.conversation-content .message .more button {
  background: transparent;
  display: inherit;
  align-items: inherit;
}
.conversation-content .message .more button svg {
  padding: 3px;
  margin: 0 2px;
  width: 22px;
  height: 22px;
}

/* ---------------conversation FOOTER FOMR --------------- */

footer {
  border-top: 1px solid rgba(128, 128, 128, 0.171);
  padding: 10px !important;
}

.form-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  height: 30px;
}

.form-wrapper > div {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
}

form .middel-bar {
  flex-grow: 1;
}
form .middel-bar input {
  height: 100%;
  background: transparent;
  color: var(--color-dark);
}
