.modal.active {
  display: block;
}
.modal {
  top: 0;
  width: 100%;
  height: 100vh;
  position: fixed;
  display: grid;
  place-items: center;
  z-index: 10;
  background-color: #0000006b;
  display: none;
}
.modal img {
  height: 100%;
  object-fit: cover;
}
.footer svg {
  padding: 5px;
  font-size: 33px;
  stroke-width: 1.4;
  /* background: #28282817; */
  border-radius: 100%;
  transition: background-color 0.2s ease-in-out;
  cursor: pointer;
}

.modal svg:hover {
  background-color: rgba(120, 86, 255, 0.144);
}

.modal .create-post-modal {
  min-height: 300px;
  max-height: 600px;
  width: 600px;
  background-color: var(--color-white);
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  border-radius: 16px;
}

.modal .user-profile {
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 100%;
  border: 1px solid #eff3f4;
  position: relative;
  display: grid;
  place-items: center;
}

.modal .user-profile::before {
  position: absolute;
  content: '';
  top: 125px;
  left: 34px;
  height: 131px;
  width: 2px;
  background: rgb(131, 0, 0);
}

.modal .user-profile img {
  object-fit: cover;
}

.modal .header {
  display: flex;
  border-bottom: 1px solid #eff3f4;
  padding: 12px;
}

.modal svg {
  display: grid;
  place-items: center;
  width: 30px;
  cursor: pointer;
  height: 30px;
  border-radius: 100%;
  padding: 4px;
}

.modal .post-status svg {
  width: 22px;
  height: 22px;
}
.modal .main {
  flex: 1;
  display: flex;
  padding: 10px;
  max-height: 600px;
  overflow-y: auto;
}

.modal .main .modal-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  min-height: 180px;
  padding: 10px;
}

.modal .main:nth-child(odd) .modal-content {
  min-height: auto;
}

.main.comment .modal-content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 10px;
  min-height: 90px;
}
.main.comment .modal-content .top-section {
  display: flex;
  align-items: center;
  gap: 10px;
}

.main.comment .post-caption {
  font-size: 15px;
}

.main.comment .modal-content .top-section .text-muted {
  color: rgba(128, 128, 128, 0.781);
  font-weight: 400;
}

.modal .main textarea,
.modal .main input.text-area {
  width: 100%;
  height: 100%;
  flex: 1;
  font-size: 17px;
  font-family: 'poppins';
  resize: none;
  padding: inherit;
  background-color: inherit;
}

/* images SET UI */
.modal .main .mediafiles-preview {
  height: 290px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 13px;
}

.modal .main .mediafiles-preview.one {
  display: block;
  height: auto;
}

.modal .main .mediafiles-preview.two {
  grid-template-areas:
    'img1 img2'
    'img1 img2';
}

.modal .main .mediafiles-preview.three {
  grid-template-areas:
    'img1 img2'
    'img1 img3';
}

.modal .main .mediafiles-preview.four {
  grid-template-areas:
    'img1 img2'
    'img3 img4';
}

.modal .mediafiles-preview.four,
.modal .mediafiles-preview.many {
  grid-template-areas:
    'img1 img2'
    'img3 img4';
  height: 400px;
}

.modal .main .media-preview:nth-child(1) {
  grid-area: img1;
}

.modal .main .media-preview:nth-child(2) {
  grid-area: img2;
}

.modal .main .media-preview:nth-child(3) {
  grid-area: img3;
}

.modal .main .media-preview:nth-child(4) {
  grid-area: img4;
}

.modal .mediafiles-preview.many .media-preview:nth-child(4):after {
  content: attr(data-count-files);
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  color: white;
  background: #0000006b;
  font-size: 3rem;
  display: grid;
  place-items: center;
}
.modal .media-preview:nth-child(n + 5) {
  display: none;
}

.modal .main .modal-content .media-preview {
  border-radius: 16px;
  overflow: hidden;
  position: relative;
}
.modal .main .modal-content .media-preview .close {
  z-index: 10;
  position: absolute;
  top: 5px;
  left: 5px;
  color: white;
  background-color: rgba(0, 0, 0, 0.37);
}
.modal .modal-content .post-status {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  padding: 10px 0 0 0px;
}
.modal .modal-content span {
  color: rgb(120, 86, 255);
  font-size: 13px;
  font-weight: 500;
}

.modal .modal-content svg {
  font-size: 27px;
  color: rgb(120, 86, 255);
}

.modal .modal-container {
  flex: 1;
}
.modal-container .footer {
  border-top: 1px solid #eff3f4;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.modal-container .footer .icons {
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
}

.modal-container .footer .icons svg {
  font-size: 30px;
  color: rgb(120, 86, 255);
  border-radius: 100%;
  width: 30px;
  height: 30px;
}

.modal-container .footer .icons svg:hover {
  background-color: rgba(120, 86, 255, 0.144);
}

.reply-to {
  margin-top: 10px;
}
