.video-chat {
  display: grid;
  place-items: center;
  height: 100vh;
  width: 100vw;
}
.videos-grid {
  display: grid;
  grid-template-columns: repeat(2, 450px);
  gap: 20px;
  align-content: center;
  justify-content: center;
  gap: 20px;
  align-items: center;
}

/* .videos-grid.two .video-container {
 
} */
.video-container.active {
  border: 2px solid var(--color-primary);
  box-shadow: 0px 0px 2px 3px #6b4ce657;
}
.video-container:hover {
  transform: scale(1.1);
  box-shadow: 0px 1px 19px 8px #00000023;
}
.video-container {
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  background-color: var(--color-white);
  display: grid;
  place-items: center;
  aspect-ratio: 16 / 9;
  cursor: pointer;
  box-shadow: 0px 1px 19px 8px #0000001a;
  transition: transform 0.2s cubic-bezier(0.39, 0.58, 0.57, 1),
    border 0.2s cubic-bezier(0.39, 0.58, 0.57, 1);
}

.videos-grid .stream {
}

.videos-grid .stream img {
  height: 100px;
  width: 100px;
  overflow: hidden;
  border-radius: 100%;
}

.video-container .btn,
.video-container .username {
  position: absolute;
  padding: 10px 20px;
  display: grid;
  place-items: center;
  padding: 0;
  background: var(--color-light);
  border-radius: 10px;
  padding: 4px 8px;
  height: 30px;
  transition: background 0.1s ease-in-out;
}
.video-chat .btn:hover,
.video-chat .username:hover {
  background: var(--color-light);
}
.video-chat .btn svg {
  width: 20px;
}

.video-chat .more-btn {
  top: 10px;
  right: 10px;
}
.video-chat .pin-btn {
  top: 10px;
  left: 10px;
}
.video-chat .mic-btn {
  bottom: 10px;
  left: 10px;
}

.video-chat .username {
  position: absolute;
  bottom: 10px;
  left: 55px;
  font-size: 14px;
  font-weight: 400;
}

.video-chat footer.btns {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 20px;
  border: none;
}

.video-chat footer.btns .btn {
  padding: 10px;
  height: 50px;
  width: 50px;
  background-color: #7e7a8b;
  box-shadow: 0px 1px 19px 8px #0000001a;
  border-radius: 100%;
  display: grid;
  place-content: center;
  cursor: pointer;
  transition: shadow 0.2s ease-in-out;
}

.video-chat footer.btns .btn:hover {
  box-shadow: 0px 1px 19px 8px #00000013;
}

.video-chat footer.btns .btn-endcall {
  height: 60px;
  width: 60px;
  border-radius: 30px;
  background-color: var(--color-danger);
}

.video-chat footer.btns .btn svg {
  color: var(--color-white);
}
