.info-section {
   margin: auto;
   height: 80vh;
   max-width: 600px;
   font-size: 20px;
   display: -ms-grid;
   display: grid;
   place-items: center;
   text-transform: capitalize;
}
.info-section-container {
   font-size: 20px;
   display: -ms-grid;
   display: grid;
   place-items: center;
   gap: 20px;
}

.info-section-container .description {
   font-size: 16px;
}

button a {
   color: white;
}
