:root {
  --bg-clr: #ffffff;
  --bg-clr-200: #f5f8fc;
  --text-clr: #10283f;
  --text-clr-200: #385470;
  --text-clr-400: #c1c4ce;
  --border-clr: #dbe5efa4;
  --border-radius: 20px;
  --padding: 30px;
  --primary-clr: #846267;
  --primary-clr: #aeb4a9;
  --primary-clr: #ff8a5b;
  --primary-clr: #8338ec;
  --primary-clr: #463f3a;
  --primary-clr: #f25f5c;
  --primary-clr: #8390fa;
  --primary-clr: #00b2ca;
  --primary-clr: #1095ff;
}

.image-container {
  width: 45px;
  height: 45px;
  overflow: hidden;
  object-fit: cover;
  border-radius: 100%;
}

.messenger {
  display: grid;
  grid-template-columns: 320px 1fr 350px;
  /* gap: 20px; */

  background-color: var(--bg-clr);
  height: 100vh;
  overflow: hidden;
}

.logo-container {
  height: 60px;
  padding: 10px 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-size: 20px;
  font-weight: bolder;
}

#logo {
  width: 50px;
}
.logo-container .logo-name {
  font-family: system-ui;
  font-weight: bolder;
  direction: rtl;
  unicode-bidi: bidi-override;
}

.logo-name::first-letter {
  color: var(--color-primary);
}

.logo img {
  height: 100%;
  width: 174px;
  object-fit: cover;
}

/*****************  chat menu *****************/
.chat-menu {
  border-right: 1px solid var(--border-clr);
}

.chat-menu-wrapper {
  display: flex;
  flex-direction: column;
  gap: 40px;
  padding: var(--padding);
  overflow: auto;
  height: 100vh;
}

.user-card {
  background-color: var(--bg-clr-200);
  border-radius: var(--border-radius);
  border: 1px solid var(--border-clr);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  min-height: 230px;
}

.profile-picture {
  width: 60px;
  height: 60px;
  border-radius: 100%;
  overflow: hidden;
}
.card-info {
  text-align: center;
}
.user-card .card-info > .name {
  color: var(--text-clr);
  font-weight: 500;
  margin: 5px 0;
}
.user-card .card-info > .caption {
  color: var(--text-clr-200);
  font-size: 14px;
  font-weight: 500;
}

/*  conversations */

.conversations-container {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.conversations .unread-messages {
  background-color: var(--bg-clr-200);
  width: 25px;
  height: 25px;
  display: grid;
  place-items: center;
  text-align: center;
  font-weight: 500;
  border-radius: 100%;
}

.conversations .title {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
}
.title h3 {
  font-size: 15px;
}

/* // user frind card active */
.user-friend-card.active {
  background-color: var(--bg-clr-200);
  color: var(--text-clr);
}
.user-friend-card.active .name {
  font-weight: 600;
}

/*  hover card soon */

.user-friend-card {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
  padding: 10px 15px;
  border-radius: var(--border-radius);
  color: var(--text-clr-200);
  cursor: pointer;
}
.user-friend-card .profile-picture {
  width: 45px;
  height: 45px;
}

.user-friend-card .card-info {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-grow: 1;
}
.user-friend-card .name {
  font-size: 14px;
  flex-grow: 1;
  text-align: left;
  font-weight: 500;
}

.user-friend-card .unread-messages {
  width: 25px;
  height: 25px;
  font-size: 13px;
  display: grid;
  place-items: center;
  text-align: center;
  font-weight: 500;
  border-radius: 100%;
  background-color: #d64242;
  color: wheat;
}

/***************** chat box ***********************/
.chat-box .navbar {
  padding: 0;
  position: absolute;
}

.chat-box .navbar-container {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  padding: 10px 15px;
  background-color: var(--bg-clr);
  box-shadow: 0px 2px 2px 0px #00000026;
}

.chat-box .navbar .left-bar .user {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 15px;
}

.chat-box .navbar .right-bar .icons {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 15px;
}

.right-bar button {
  background-color: transparent;
}

.chat-box {
  position: relative;
  background-color: var(--bg-clr-200);
  background-color: var(--bg-clr);
  border-radius: var(--border-radius);
  height: 100vh;
  width: 100%;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}
.chat-box .no-messages {
  text-align: center;
  font-size: 30px;
  color: rgba(72, 78, 85, 0.651);
  margin-top: 100px;
}

.chat-box p {
  text-align: center;
  font-size: 20px;
  color: rgba(100, 105, 112, 0.781);
}

.chat-box .msg {
  text-align: center;
  font-size: 15px;
  color: rgba(97, 102, 107, 0.308);
  margin-top: 100px;
}

.chat-box-wrapper {
  flex-grow: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 50px;
  border-radius: 20px;
  max-height: 100%;
  overflow-y: scroll;
  margin-top: 40px;
}

.message-contianer .image-container {
  width: 35px;
  height: 35px;
  border-radius: 100%;
  overflow: hidden;
}
.message-contianer {
  display: flex;
  gap: 10px;
  justify-content: flex-start;
  align-items: flex-end;
}

.message-contianer .message {
  background: #f6f7f7;
  padding: 9px 20px;
  border-radius: 20px 20px 20px 0px;
  color: #272548;
}
.message-contianer .message .text-message {
  font-size: 14px;
}

.message-contianer.own {
  flex-direction: row-reverse;
  align-items: flex-end;
}
.message-contianer.own .message {
  background-color: var(--primary-clr);
  color: white;
  font-weight: 300;
  border-radius: 20px 30px 0px 20px;
}

/* chat input  */

.chat-form {
}

.chat-form-wrapper {
  /* height: 100px; */
  /* background-color: #54687a33; */
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 30px;
  padding: 20px;
  border-top: 1px solid var(--border-clr);
}

.chat-form .left-bar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
.chat-form .middel-bar {
  flex: 1;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 15px;
}

.chat-form .middel-bar input[type='text'] {
  width: 100%;
  height: 45px;
  border-radius: 8px;
  background-color: var(--bg-clr-200);
  padding: 3px 20px 0px 20px;
  resize: none;
  font-family: 'poppins';
  color: var(--text-clr);
  border: 1px solid #3d3d3d13;
}
.chat-form .input-message:focus {
  border: 1px solid var(--primary-clr) !important;
}

.chat-form .middel-bar textarea::placeholder {
  color: rgb(151, 159, 168);
}

.chat-form .right-bar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 15px;
}

/********************* chat online ***************** */

.chat-online {
  border-left: 1px solid var(--border-clr);
  overflow: auto;
  height: 100vh;
}

.chat-online-wrapper {
  padding: var(--padding);
}
.chat-online header {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.chat-online .users-in-chat {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.chat-online .users-images {
  display: flex;
}

.chat-online header .image-container {
  width: 70px;
  height: 70px;
  border: 5px solid var(--bg-clr);
}

.chat-online header .image-container:first-child {
  margin-right: -15px;
  z-index: 1;
}

.chat-online header .group-name {
  font-weight: 500;
  color: var(--text-clr);
  margin-top: 10px;
}

.chat-online header .status {
  font-size: 14px;
  color: var(--text-clr-200);
}

.chat-online header .btns {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

.chat-online header .btns button {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  padding: 10px 15px;
  font-weight: 500;
  border-radius: 5px;
  color: var(--primary-clr);
  gap: 10px;
  background-color: var(--bg-clr-200);
}

.chat-online header .btns svg {
  color: var(--primary-clr);
  font-size: 20px;
}

.chat-online header .params-list {
}

/* chat online footer */
footer .shared-photos {
  margin-top: 50px;
}

footer .title {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  font-size: 14px;
  color: var(--text-clr);
  margin-bottom: 20px;
}
footer .title h4 {
  font-weight: 500;
}

footer .shared-shotos-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 55px;
  align-content: center;
  justify-content: center;
  align-items: center;
  justify-items: center;
  gap: 10px 6px;
}

footer .shared-shotos-grid .image-container {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

a.view-more {
  font-size: 14px;
  color: #1095ff;
  display: block;
  text-align: center;
  padding: 15px;
}

/* messenger theme */
.chat-online header .params-list > li {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  color: var(--text-clr-200);
  font-size: 14px;
  padding: 13px;
  border-bottom: 1px solid var(--border-clr);
}

.messenger-color-theme {
  cursor: pointer;
}

.chat-online header .color-theme {
  height: 20px;
  width: 20px;
  border: 3px solid white;
  box-shadow: 1px 3px 3px #80808069;
  background-color: var(--primary-clr);
  border-radius: 100%;
  position: relative;
}

.chat-online header .color-theme-list.active {
  height: 38px;
  opacity: 1;
}

.chat-online header .color-theme-list {
  position: absolute;
  overflow: hidden;
  top: 25px;
  right: -15px;
  min-width: 120px;
  padding: 0 15px;
  height: 0;
  opacity: 0;
  background-color: #1c1c1c;
  border-radius: 10px;
  transition: height 0.1s ease-in, opacity ease 0.2s;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  gap: 7px;
  box-shadow: 1px 3px 3px #80808069;
  cursor: default;
}

.chat-online header .color-theme-list > li {
  height: 18px;
  width: 18px;
  border: 1px solid white;
  border-radius: 100%;
  position: relative;
  padding: 5px;
  cursor: pointer;
}

