.alert-card label {
   font-size: 12px;
}
.alert-card label.focus .input-placeholder {
   position: absolute;
   top: -17px;
   left: 15px;
   font-size: 13px;
}

.alert-card .input-placeholder {
   position: absolute;
   position: absolute;
   left: 23px;
   font-size: 13px;
   top: 50%;
   transform: translateY(-50%);
   pointer-events: none;
   transition: all 0.1s;
}

.alert-card input {
   width: 100%;
   height: 100%;
   padding: 10px 10px;
   border-radius: 10px;
   background: var(--color-light);
   color: var(--color-dark);
   border: 2px solid transparent;
   font-size: 14px;
   font-family: inherit;
   font-weight: 400;
   text-transform: capitalize;
}

.alert-card .input-error-message {
   display: none;
   color: red;
   padding: 7px;
   border-radius: 5px;
   overflow: hidden;
   display: none;
   color: red;
   /* background: #efa3a636; */
   padding: 2px 9px;
   max-height: 20px;
   border-radius: 5px;
   position: absolute;
   top: 58px;
   width: 110%;
}
.alert-card input:invalid[focused="true"] ~ span {
   display: block;
}
.alert-card input:invalid[focused="true"] {
   border: 2px solid var(--color-danger);
   box-shadow: 0px 0px 2px 4px #e64c4c49;
}
.alert-card input::placeholder {
   color: var(--color-gray);
   font-weight: 400;
   font-size: 15px;
}
.alert-card input:focus {
   border: 2px solid var(--color-primary);
   box-shadow: 0px 0px 2px 4px #6b4ce649;
}
