.user-card-popup.active {
  opacity: 1;
  visibility: visible;
}
.user-card-popup {
  position: absolute;
  /* for showing date */
  top: 42px;
  left: 20px;
  /*  */
  top: 30px;
  left: 25px;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s 0.3s ease-in-out, visibility 0.2s 0.1s;
}
.user-card-popup-container {
  max-height: 270px;
  min-width: 280px;
  border-radius: 20px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  box-shadow: 1px 1px 6px #2d2d2d6e;
}

.user-card-popup .header {
  display: flex;
  justify-content: space-between;
}

.user-card-popup .username {
  text-transform: capitalize;
  font-weight: 500;
}
.user-card-popup .username:hover {
  text-decoration: underline;
}

.user-card-popup .caption {
  margin-top: 10px;
  max-height: 65px;
  overflow: clip;
}

.user-card-popup .header .btn {
  height: 35px;
}

