span.edit {
  position: relative;
  cursor: pointer;
}

.middle .feeds .feed.big-text {
  display: flex;
  flex-direction: column;
}
.middle .feeds .feed.big-text .action-buttons {
  order: 2;
  margin-left: 0;
}

.middle .feeds .feed.big-text .caption {
  order: 1;
  margin-top: 20px;
  margin-bottom: 10px;
}

.middle .feeds .feed.big-text .caption b,
span.harsh-tag {
  display: none;
}

.middle .feeds .feed.big-text .comments.text-muted {
  order: 4;
  user-select: none;
}

/* images SET UI */
.feed .mediafiles-preview img {
  width: 100%;
  object-fit: cover;
  height: 100%;
  max-height: 650px;
}
.feed .mediafiles-preview {
  margin: 10px 0;
  height: 450px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 13px;
}

.feed .mediafiles-preview.one {
  display: block;
  height: auto;
}

.feed .mediafiles-preview.two {
  grid-template-areas:
    'img1 img2'
    'img1 img2';
}

.feed .mediafiles-preview.three {
  grid-template-areas:
    'img1 img2'
    'img1 img3';
}

.feed .mediafiles-preview.four,
.feed .mediafiles-preview.many {
  grid-template-areas:
    'img1 img2'
    'img3 img4';
  height: 430px;
}

.feed .mediafiles-preview.many .media-preview:nth-child(4):after {
  content: attr(data-count-files);
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  color: white;
  background: #0000006b;
  font-size: 3rem;
  display: grid;
  place-items: center;
}

.feed .media-preview:nth-child(1) {
  grid-area: img1;
}

.feed .media-preview:nth-child(2) {
  grid-area: img2;
}

.feed .media-preview:nth-child(3) {
  grid-area: img3;
}

.feed .media-preview:nth-child(4) {
  grid-area: img4;
}

.feed .media-preview:nth-child(n + 5) {
  display: none;
}

.feed .media-preview {
  border-radius: 16px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  transition: filter 0.1s ease-in-out;
}

/* .feed .media-preview:hover {
   filter: brightness(80%);
} */

.feed .media-preview .close {
  z-index: 10;
  position: absolute;
  top: 5px;
  left: 5px;
  color: white;
  background-color: rgba(0, 0, 0, 0.37);
}
